.owner-employee{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .title-employee{
        width: 95%;
        margin: auto;
        @include grid(4, 1rem);
        h1{
            font-size: 2.5rem;
            color: $principal;
        }
    }
    .employee{
        width: 95%;
        margin: auto;
        @include grid(4, 1rem);
        border-bottom: 1px solid $principal;
        justify-content: center;
        place-items: center;
        p{
            margin: 0;
        }
    }
    .header-employee{
        .btn-new-employee{
            width: 20%;
            margin: auto 0 auto auto;
        }
    }
    
    .actions-employees{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        button{
            margin: 1rem 0;
            width: min-content;
            height: min-content;
        }
        .btn-remove{
            margin-left: 2.5%;
        }
    }
    .select-branch-admin{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin-left: 5%;
        margin-top: 2rem;
        margin-bottom: 4rem;
        p{
            margin: 0;
        }
        select{
            margin-left: 1rem;
            height: min-content;
            border-color: $principal;
            background-color: white;
            border-radius: 2rem;
            text-align: center;
        }
    }
    .branch-checkbox{
        display: flex;
        flex-direction: row-reverse;
        margin-bottom: 1rem;
        input{
            margin: 0;
        }
        label{
            width: 50%;
        }
    }
    
    .add-new-employee{
        width: 90%;
        margin: 0;
    }
    @include telefono(){
        display: flex;
        flex-direction: column;
        .title-employee{
            display: none;
        }
        .employee{
            display: flex;
            p{
                margin: 0;
            }
        }
        .header-employee{
            .btn-new-employee{
                width: 90%;
                margin: auto;
            }
        }
        .modal-content{
            height: 50vh;
            overflow-y: scroll;
        }
    }
}
